.hero {
  position: relative;
  height: 850px;
  background-size: cover;
  background-position: center;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero-content {
  text-align: center;
  max-width: 600px;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 14px;
  box-shadow: 0 0 10px white;
}

.hero h1 {
  font-size: 3rem;
  margin-bottom: 10px;
  color: white;
}

.hero p {
  font-size: 1.5rem;
  margin-bottom: 20px;
}

@media (max-width: 768px) {
  .hero {
    height: 350px;
    background-size: contain;
  }

  .hero-content {
    padding: 15px;
    max-width: 90%;
  }

  .hero h1 {
    font-size: 1.8rem;
  }

  .hero p {
    font-size: 0.9rem;
  }
}
